var recentProjects=[0,1];var recentSideProjects=[];var projects=[{id:1,title:'Channeli Core',tags:['Frontend','Team Project'],role:'Front-end Development',technical:'React, Redux, Semantic UI, SCSS',type:'Information Management Group',period:'November 2017',description:"Channeli is the official intranet application providing various online services and solutions for students, faculty and alumni of the institute. The aim of the project is to re-design the current application including the front-end architecture. We are planning to build the application using React+Redux with Semantic UI. The app is quite extensive as it hosts many internal services like Calendar & Events, Notices, User Profile and Helpdesk. The project is currently in-progress.",short:'Re-design and re-structure of Core Channeli to a React + Redux stack',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/channeli/hero.jpg?raw=true',theme:'#ad6cc6',isOngoing:'true'},{id:2,title:'Connect-e-dil',tags:['Frontend','Team Project'],role:'Front-end Development',technical:'React, Redux, Sockets, Semantic UI, SCSS',type:'Information Management Group',period:'February 2018',description:"They invented hugs to let people know you love them without saying anything. Likewise, we invented Connect-e-dil. As the tradition goes, send roses, make connections and spread the ❤. If there was a couple meant to be, one can go ahead and suggest a match using the 'Make a Pair feature'. This app had to be built within 3 days, and we succeeded in doing so. It was built using React+Redux and also had a real-time shoutbox feature, built with Sockets. The app had almost 4.5K registered users and more than 30K pageviews.",short:"A fun app launched during the Valentines' Week where one could send roses and nominate couples",image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/connect-e-dil/hero.jpg?raw=true',theme:'#ff667c'},{id:3,title:'Channeli Registration',tags:['Frontend','Team Project'],role:'Front-end Development',technical:'React, Semantic UI',type:'Information Management Group',period:'July 2017',description:"A simple registration portal for Channel i for the freshers to get their account set up. The application was built using React along with Semantic UI, which inherently has a very elegant UI. Every year around 1100 students register on Channeli, the intranet e-portal providing various services for the students, faculty and alumni of the institute.",short:'An online portal to facilitate Channeli Registration of the freshers, which in the previous years was a tedious paperwork',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/registration/hero.jpg?raw=true',theme:'#fcc02e'},{id:4,title:'IITR Fresherlogue',tags:['Product Design','Frontend','Team Project'],role:'Product Design & Front-end Development',technical:'Angular JS, Masonry, MaterializeCSS',type:'Information Management Group',period:'December 2016',description:"IITR Fresherlogue is a platform for the freshers to know about the Institute by creating a bridge between them and the seniors. The project included developing applications for web and mobile. I was responsible for the UX design and front-end development. The user experience is based on Material Design, which feels straight-forward yet sophisticated. The web app was built using AngularJS 1.x and Materialize CSS.",short:'A platform to help the freshers get a proper insight about the Institute by providing a medium for them to interact with existing students and authorities.',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/fresherlogue/hero.jpg?raw=true',theme:'#faffff'},{id:5,title:'Unnat Bharat Abhiyan',tags:['Frontend','Freelance'],role:'Front-end Development',technical:'Vanilla JS, Semantic UI',type:'Freelance',period:'November 2016',description:"A clean and elegant website showcasing the accomplishments, projects done and initiatives taken by the IIT Roorkee chapter of Unnat Bharat Abhiyan (UBA). UBA is an MHRD, Government of India programme to uplift rural India. The team has identified 5 villages and is working in continuous coordination with the village officials in identifying development challenges and evolving appropriate solutions for accelerating sustainable growth.",short:'Portfolio showcasing the projects and case studies taken up by IIT Roorkee Chapter of Unnat Bharat Abhiyan',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/uba/hero.jpg?raw=true',theme:'#ee534f',showcaseText:'Visit Website',showcaseLink:'https://www.iitr.ac.in/subdomain/uba/'},{id:6,title:"Recruitment '18",tags:['Typography','Frontend','Team Project'],role:'Product Design & Front-end Development',technical:'Vanilla JS, Bootstrap',type:'Information Management Group',period:'January 2018',description:'A creative and elegant website for the recruitment season of IMG. The site had to be professional in both language and design. Every section has its own essence of typography which adds a flair to the entire experience.',short:'Promotional website for the recruitment drive of Information Management Group',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/recruitment/hero.jpg?raw=true',theme:'#ffc928'},{id:7,title:'Helpdesk',tags:['Frontend','Team Project'],role:'Product Design & Front-end Development',technical:'jQuery, Materialize CSS, Django',type:'Information Management Group',period:'February 2016',description:'A simple and utility-focussed help and support portal for various applications and services in Channel i (campus e-portal of IIT Roorkee). The application consists of well-documented FAQs of various services. An admin portal too is implemented for content management.',short:'Helpdesk comprises of well-documented FAQs and currently facing/unresolved issues of the services in Channeli',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/01-hero.png?raw=true',theme:'#66bb6a'},{id:8,title:'Noticeboard',tags:['Product Design','Mobile App','Team Project'],role:'Product Design',technical:'Material Design, Adobe Illustrator, InVision App',type:'Information Management Group',period:'February 2016',description:'Noticeboard provides a medium for the residents of IIT Roorkee to get information about any announcements, notices and events by various Departments and Insititute authorities. This is my first product design project which helped me understand what "user comes first" meant',short:'Channeli Noticeboard is a medium for the students to access the notices posted by various institute authorities',image:'https://github.com/theindianotaku/portfolio-assets/blob/master/projects/noticeboard/hero.jpg?raw=true',theme:'#609bf7',showcaseText:'View app on Play Store',showcaseLink:'https://play.google.com/store/apps/details?id=com.channeli.noticeboard'}];var sideProjects=[{id:1,title:'Type Master',link:'https://github.com/theindianotaku/react-type-master'},{id:2,title:'Expense-ify',link:'https://github.com/theindianotaku/react-expensify-app'},{id:3,title:'The Indecision',link:'https://github.com/theindianotaku/react-indecision-app'}];

